@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
/*Make the scrolling smooth*/
* {
  scroll-behavior: smooth;
}
body {
  padding: 0;
  margin: 0;
  width: 100%;
  background-image: linear-gradient(to right, #c7d8e7, rgb(248, 248, 255));
}

.App {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #53565a;
}

.App form {
  display: block;
  position: relative;
}

.App form:after {
  content: "";
  display: block;
  position: absolute;
  top: -5px;
  left: -5px;
  bottom: -5px;
  right: -5px;
  z-index: 1;
  background-image: linear-gradient(
    to bottom right,
    rgb(127, 255, 212),
    #c7d8e7
  );
}

.App form .form-inner,
.register {
  position: relative;
  display: block;
  background-color: #fff;
  padding: 30px;
  z-index: 2;
}

.App form .form-inner h2 {
  color: #888;
  font-size: 28px;
  font-weight: 500;
  margin-bottom: 30px;
}

.App form .form-inner .form-group {
  display: block;
  width: 300px;
  margin-bottom: 15px;
}

.form-inner .form-group label {
  display: block;
  color: #000;
  font-size: 14px;
  margin-bottom: 5px;
  transition: 0.4s;
}
.form-inner .form-group:focus-within label {
  color: #0d659d;
}

.App form .form-inner .form-group input,
.register {
  display: block;
  width: 100%;
  padding: 10px 0px;
  background-color: #f8f8f8;
  border-radius: 8px;
  transition: 0.4s;
}

.App form .form-inner .form-group input:focus,
.register:focus {
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
}

.App form .form-inner input[type="submit"],
.welcome button,
.register {
  display: inline-block;
  padding: 10px 15px;
  border-radius: 8px;
  background-image: linear-gradient(
    to right,
    #6bcae2 50%,
    #6bcae2 50%,
    #0d659d
  );
  background-size: 200%;
  background-position: 0%;
  transition: 0.4s;
  color: #fff;
  font-weight: 700;
  cursor: pointer;
}

.App form .form-inner input[type="submit"]:hover,
.welcome button:hover,
.register:hover {
  background-position: 100% 0%;
}

.error {
  text-align: center;
  color: red;
}

.welcome {
  width: 100%;
  max-width: 480px;
  background-color: #fff;
  padding: 15px 30px;
}

.welcome h2 {
  text-align: center;
  color: #888;
  font-size: 28px;
  font-weight: 500;
  margin-bottom: 30px;
}

.welcome h2 span {
  color: #0d659d;
  font-weight: 700;
}

.register {
  position: relative;
  width: 100px;
  margin-left: 130px;
  background-image: linear-gradient(
    to right,
    #0d659d 50%,
    #0d659d 50%,
    #6bcae2
  );
}

/* Nav bar general layout */

.navbarr nav {
  padding: 0;
  width: 100%;
  top: 0;
  z-index: 20;
  background-color: rgb(127, 255, 212);
  display: block;
  background-image: linear-gradient(to right, rgb(127, 255, 212), #c7d8e7);
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
}
/*Make the scrolling smooth*/
* {
  scroll-behavior: smooth;
}

.topnav {
  position: fixed;
  border-bottom: 1px solid #c7d8e7;
  height: 50px;
  position: fixed;
  top: 0;
  z-index: 99;
}

.topnav a {
  top: 0;
  float: right;
  padding: 5px 5px 5px 5px;
  margin: 13px 5px 5px 5px;
  text-decoration: none;
  color: black;
}
.topnav a:hover {
  border-radius: 10px;
  color: #000000;
  background-color: #7fffd4;
  text-decoration: none;
}

.bottomnav {
  border-top: 1px solid #c7d8e7;
  background-image: linear-gradient(to left, rgb(127, 255, 212), #c7d8e7);
  height: 50px;
  position: static;
  bottom: 0;
  width: 100%;
  margin: 0;
}
.bottomnav a {
  bottom: 0;
  float: left;
  padding: 5px 5px 5px 5px;
  margin: 13px 5px 5px 5px;
  text-decoration: none;
  color: black;
}

.bottomnav a:hover {
  border-radius: 10px;
  color: #000000;
  background-color: #7fffd4;
}
.signin a {
  border-style: outset;
  text-align: center;
  text-decoration: none;
  padding: 5px 30px 5px 30px;
  margin: 10px 5px 5px 5px;
  border-radius: 10px;
  cursor: pointer;
}
.signin a:hover {
  background-color: #7fffd4;
  box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19);
  color: black;
}

.logo {
  font-family: "Lobster", cursive;
  font-size: 20px;
  margin: 0 30px;
  padding: 11px 0;
  float: left;
}

.logo a {
  padding: 0;
  margin: 0;
  color: #0d659d;
}

.logo a:hover {
  color: #000;
}

#myBtn {
  /*    smooth scroll to top instead of jump*/
  scroll-behavior: smooth;
  display: none;
  position: fixed;
  text-align: center;
  width: 60px;
  height: 20px;
  bottom: 45px;
  right: 30px;
  z-index: 99;
  font-size: 15px;
  border: ridge;
  outline: none;
  background-color: #7fffd4;
  color: #000000;
  cursor: pointer;
  padding: 5px 15px 20px 15px;
  border-radius: 10px;
  border: inset;
}
/*Username dropdown list css*/
.topnav .navbar-dropdown a {
  margin: 12px 35px 10px 20px;
  background-color: transparent;
  border: none;
}
.topnav .navbar-dropdown-elements {
  overflow: hidden;
  max-height: 0;
  position: absolute;
  right: 1%;
  transition: max-height 0.7s ease;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
  padding: 35px 0px 0px;
  margin: 10px 0px 0px 0px;
  border-radius: 10px;
}
.topnav .navbar-dropdown-elements a {
  display: block;
  float: none;
  text-decoration: none;
  color: black;
  padding: 10px 5px 0px;
  text-align: center;
  margin: 5px;
  background-color: #c7d8e7;
}

.topnav .navbar-dropdown:hover .navbar-dropdown-elements:hover {
  max-height: 250px;
  border: solid 1px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

#content {
  margin: 0px 60px 10px 65px;
  background-image: linear-gradient(to right, #c7d8e7, rgb(248, 248, 255));
}
#content .dropdown {
  float: right;
  padding: 5px;
  border: solid 1px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
}
.dashboard hr {
  width: 100%;
}
.dashboard header {
  font: bold 25px verdana;
  width: 100%;
  margin: 0px 0px 15px 0px;
}
.dashboard h2 {
  font: bold 15px verdana;
  float: right;
  margin: 5px 10px 0px 0px;
}
.dashboard h1 {
  float: left;
  display: inline-block;
  font: bold 20px verdana;
  width: 37%;
  margin: 20px 0px 0px 0px;
}
#profile {
  height: 100px;
  display: inline-block;
  float: left;
  width: 50%;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
  border: solid 1px rgba(0, 0, 0, 0.2);
  border-radius: 25px;
}
.daily-challenge {
  width: 50%;
  height: 100px;
  clear: right;
  margin-top: 10px;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
  border: solid 1px rgba(0, 0, 0, 0.2);
  border-radius: 25px;
}
#leaderboard {
  width: 40%;
  background-color: #7fa6c9;
  color: #fdfaf5;
  min-height: 100px;
  display: inline-block;
  margin-left: 30px;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
  border: solid 1px rgba(0, 0, 0, 0.2);
  border-radius: 25px;
  text-align: center;
  font: bold 15px verdana;
  padding-top: 5px;
  padding-bottom: 5px;
}
#leaderboard ol {
  list-style-type: none;
  margin: 10px 0px 20px 0px;
  padding: 0px;
}
#leaderboard li {
  text-align: left;
  width: 92%;
  margin: 7.5px 10px 7.5px 10px;
  padding: 10px 0px 10px 25px;
  background-color: #050b1f;
  color: #fdfaf5;
  text-decoration: none;
  font: 15px verdana;
  overflow: hidden;
  border-radius: 5px;
}

#leaderboard li:nth-child(1) {
  background-image: linear-gradient(to right, gold 0%, #050b1f 3%);
  color: gold;
}

#leaderboard li:nth-child(2) {
  background-image: linear-gradient(to right, silver 0%, #050b1f 3%);

  color: silver;
}
#leaderboard li:nth-child(3) {
  background-image: linear-gradient(to right, #cd7f32 0%, #050b1f 3%);

  color: #cd7f32;
}

#content .dropdown-elements {
  overflow: hidden;
  opacity: 0;
  max-height: 0;
  z-index: 1;
  padding: 5px 50px 5px 5px;
  margin: 5px;
  position: absolute;
  right: 3%;
  transition: opacity 0.5s ease-out, max-height 0.3s ease;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
  border: solid 1px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}
#content .dropdown-elements a {
  display: block;
  float: none;
  text-decoration: none;
  padding: 5px;
  margin: 5px;
  color: black;
  text-align: right;
}
#content .dropdown:hover .dropdown-elements {
  opacity: 1;
  max-height: 100%;
}
#content .category {
  width: 100%;
  height: 110px;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
  border: solid 1px rgba(0, 0, 0, 0.2);
  border-radius: 25px;
  margin: 0px 0px 30px 0px;
  text-align: center;
  font: 15px verdana;
}
#content .arrow {
  display: inline-block;
  border: solid black;
  border-width: 0px 4px 4px 0px;
  padding: 5px;
  margin: 0px 0px -3px 0px;
}
#content .right {
  float: right;
  margin: 10px 10px 0px 0px;
  transform: rotate(315deg);
}
#content .left {
  float: left;
  margin: 10px 0px 0px 10px;
  transform: rotate(-225deg);
}
#content #js-category-name {
  display: inline-block;
}
#content .game {
  width: auto;
  float: left;
  height: 75px;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
  border: solid 1px rgba(0, 0, 0, 0.2);
  border-radius: 25px;
  margin: 5px 20px 10px 20px;
}
#content .continue {
  width: 100%;
  height: 100px;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
  border: solid 1px rgba(0, 0, 0, 0.2);
  border-radius: 25px;
  margin: 0px 0px 30px 0px;
}
#content .search-bar {
  float: left;
  display: inline-block;
  width: 60%;
  margin: 20px 0px 0px 0px;
}
#content .search-button {
  margin: 21.2px 0px 15px -20px;
  width: 19px;
}

.confirm-pass {
  background-color: white;
  margin: auto;
  padding: 0;
  font-family: arial;
  font-size: 15px;
  width: 50%;
  height: 100%;
}

/* Full-width input fields */
.confirm-pass input[type="text"],
input[type="password"] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

/* Set a style for all buttons THIS IS CAUSING ERRORS IN THE SETTINGS PAGE*/
.confirm-pass button {
  background-color: #7fffd4;
  color: #000000;
  padding: 10px 40px;
  margin: 8px 0;
  border: outset;
  cursor: pointer;
  width: 100%;
  border-radius: 10px;
}

.confirm-pass button:hover {
  opacity: 0.8;
  box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19);
  color: black;
}

.container {
  width: 100%;
}

.confirm-pass h1 {
  color: black;
}

.confirm-pass fieldset {
  padding: 150px;
  border: none;
  margin: auto;
}

@charset "utf-8";
/* CSS Document */
.navbar-dropdown-elements:hover {
  background: linear-gradient(to bottom, #ffffff00 30%, #ffffff90 10%);
}

.profile * {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  height: 100%;
}

html {
  scroll-behavior: smooth;
}

:root {
  --colscheme1: #050b1f;
  --colscheme2: #6bcae2;
  --colscheme3: #0d659d;
  --colscheme4: #c7d8e7;
  --colscheme5: #11f7ff;
}

#profilebody {
  font-family: "Calibri Light", sans-serif;
  background: black;
  background: url(/static/media/pbg-dark.e493db53.png);
  background-attachment: fixed;
  background-size: cover;
}

.profile footer {
  background: #ffffff50;
  height: 250px;
  display: flex;
  color: white;
}
.profile footer p {
  margin: auto;
  font-style: italic;
}
#ft-note {
  margin: auto;
}
.profile-content {
  width: 80%;
  padding: 100px 0 100px 0;
  margin: 0 auto;
  display: flex;

  justify-content: flex-end;
  flex-wrap: wrap;
  border-radius: 30px;
}

.profile .p-head {
  display: flex;
  flex-basis: 100%;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-bottom: 20px;
  padding: 20px 50px;
  padding-bottom: 50px;
  background: linear-gradient(to bottom, #0d659d, #affcff, #ffffff00);
  background: linear-gradient(to bottom, var(--colscheme3), #affcff, #ffffff00);
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.profile #user-pfp {
  background: linear-gradient(to bottom, #c7d8e7, #6bcae2);
  background: linear-gradient(to bottom, var(--colscheme4), var(--colscheme2));
  width: 300px;
  height: 300px;
  border-radius: 50%;
  border: 5px #0d659d solid;
  border: 5px var(--colscheme3) solid;
  box-shadow: 0px 5px 5px black;
  transition: border 0.1s ease-in;
  overflow: hidden;
}

.profile #name-title {
  font-size: 70px;
  color: white;
}
.profile #user-pfp img {
  max-height: 100%;
  max-width: 100%;
}
.profile #user-pfp:hover {
  border: 10px #0d659d solid;
  border: 10px var(--colscheme3) solid;
}

.profile #user-infosec {
  padding: 50px;
}

.profile #circle-stats {
  margin-left: auto;
  width: 250px;
  height: 250px;
  background: linear-gradient(to bottom, #c7d8e7, #6bcae2);
  background: linear-gradient(to bottom, var(--colscheme4), var(--colscheme2));
  border-radius: 50%;
  display: flex;
  position: relative;
  box-shadow: 0px 5px 5px #0d659d;
  box-shadow: 0px 5px 5px var(--colscheme3);
}

.profile #circle-stats:hover .circle-xp {
  stroke: #0b7ac0;
}
.profile .div-lvl {
  text-align: center;
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
}

.profile .div-lvl div {
  background: linear-gradient(to bottom, #0d659d, #6bcae2);
  background: linear-gradient(to bottom, var(--colscheme3), var(--colscheme2));
  width: 70%;
  height: 70%;
  border-radius: 50%;
  display: flex;
  margin: auto;
  border: 2px solid #c7d8e7;
  border: 2px solid var(--colscheme4);
}

.profile #circle-stats h3 {
  padding: 10px;
  margin: auto;
  font-size: 30px;
}

.profile .circle-xp {
  fill: transparent;
  stroke: #0d659d;
  stroke: var(--colscheme3);
  stroke-width: 40;

  transition: stroke 0.2s linear;
}

.profile #circle-stats svg {
  width: 100%;
}
.profile svg {
  transform: rotate(-90deg);
}

.profile section {
  background: #ffffff90;
}

.profile .profile-div-left {
  flex-basis: 25%;
  display: flex;
  margin-right: 25px;
}
.profile .profile-div-right {
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 25%;
  margin-left: 10px;
  width: 100%;
}

.profile .pf-sec {
  width: 100%;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0px 5px 5px #6bcae2;
  box-shadow: 0px 5px 5px var(--colscheme2);
}

.profile .sec-badges {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 25px;
  padding-bottom: 20px;
}

.profile .ubadge {
  height: 100px;
  width: 100px;
  margin: 20px;
  border-radius: 100%;
  background: #ffffff90;

  position: relative;
  text-align: center;
  overflow: hidden;

  transition: all 0.1s ease-out;
}
.profile .ubadge img {
  max-width: 100%;
  max-height: 100%;
}

.profile .profile.ubadge h5 {
  color: white;
  position: relative;
  bottom: 55px;
}

.profile .ubadge:hover {
  box-shadow: 0px 5px 5px #0d659d;
  box-shadow: 0px 5px 5px var(--colscheme3);
  transform: scale(1.1);
}

.profile .sec-most-played {
  display: flex;
  flex-wrap: wrap;
}

.profile .game {
  min-width: 200px;
  flex-grow: 1;

  height: 100px;
  margin: 20px;

  background: #ffffff90;
  border-radius: 20px;
  text-align: center;
  transition: all 0.1s ease-out;
  display: flex;
  cursor: pointer;
}

.profile .game h5 {
  margin: auto;
}
.profile .game:hover {
  transform: scale(1.05);
  box-shadow: 0px 5px 5px #0d659d;
  box-shadow: 0px 5px 5px var(--colscheme3);
}

.profile .frn {
  height: 70px;
  background: #ffffff90;
  padding: 0 10px;
  display: flex;
  justify-content: center;
  margin: 10px;
  transition: all 0.1s ease-out;
  position: relative;
}

.profile .frn:hover {
  cursor: pointer;
  text-decoration: underline;
  box-shadow: 0px 5px 5px #0d659d;
  box-shadow: 0px 5px 5px var(--colscheme3);
}
.profile .frn:nth-child(even) {
  background: #6da7e550;
}
.profile .frn-img {
  background: #ffffff90;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin: auto 5px;
}

.profile .frn h5 {
  margin: auto;
}
.profile .profile h4 {
  flex-basis: 100%;
  padding: 20px;
  font-size: 30px;
  color: white;
  background: linear-gradient(#6bcae2, #c7d8e7, #ffffff);
  background: linear-gradient(var(--colscheme2), var(--colscheme4), #ffffff);
}

.profile .bio {
  color: Blue;
  border: 1px solid Blue;
}

@media (max-width: 1340px) {
  .profile #user-pfp {
    width: 200px;
    height: 200px;
  }
  .profile #user-infosec {
    margin-right: auto;
  }
}

@media (max-width: 1200px) {
  .profile #circle-stats {
    display: none;
  }
  .profile .profile-div-left {
    flex-basis: 100%;
    margin: 0;
    padding: 0;
    margin-bottom: 25px;
  }
  .profile .profile-div-right {
    flex-basis: 100%;
    margin: 0;
  }
  .profile .sec-friends {
    display: flex;
    flex-wrap: wrap;
  }
  .profile .frn {
    flex-basis: 32%;
    margin: 5px;
  }
}
@media (max-width: 1000px) {
  .profile .frn {
    flex-basis: 48%;
    margin: 5px;
  }
}
@media (max-width: 900px) {
  .profile #user-pfp {
    margin: auto;
  }
  .profile #user-infosec {
    margin: auto;
    padding: 20px 50px;
  }
}
@media (max-width: 650px) {
  .profile .frn {
    flex-basis: 100%;
    margin: 5px;
  }
  .profile .ubadge {
    margin: 10px;
  }
}

#ifgame_body {
	position:relative;
	top: 0px;
	bottom: 0px;
	left: 0px;
	right: 0px;
	width: 100%;
	margin: 0px;
	padding: 0px;
	padding-bottom: 50px;
	padding-top: 10px;
	font-family: sans-serif;
	background-image: linear-gradient(to right, #c7d8e7, rgb(248,248,255));
}

.ifgame_pre {
	color: inherit;
	font-size: 100%;
}

.ifgame_h1 {
	font: bold 30px verdana;
	padding-top: 20px;
	padding-bottom: 20px;
}

.ifgame_h2 {
	font: bold 22.5px verdana;
	text-align: center;
	background-color: #0d659d;
	color: #fbfaf5;
	margin-top: 0px;
	padding-top : 10px;
	padding-bottom: 10px;
}


.ifgame_button {
	display: block;
	text-align: center;
	font: normal 15px verdana;
	width: 100%;
	padding-top: 10px;
	padding-bottom: 10px;
	margin-top: 15px;
	margin-bottom: 15px;
	box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.2);
	border: solid 1px rgba(0,0,0,0.2);
}

#content {
	margin: 0px 60px 10px 65px;
}

/* Option menus */
#option-select {
	margin-right: 20px;
}

.option {
	margin-bottom: 20px;
}

.option > .ifgame_select {
	box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.2);
	border: solid 1px rgba(0,0,0,0.2);
	padding: 10px 5px 10px 5px;
	font: bold 14px verdana;
	margin-left: 5px;
}

#game-window {
	position: relative;
	display: grid;
	grid-template-columns: 25% 50% auto;
	grid-template-rows: 575px;
}

#game-grid {
	display: grid;
	grid-template-columns: auto auto auto;
}

.grid-position {
	display: flex;
	justify-content: center;
	align-items: center;
	height: auto;
	width: auto;
	border: 1px solid gray;
	text-align: center;
	background-color: #555;
}

#robot {
	position: absolute;
	width: 8%;
}

#goal {
	position: absolute;
	width: 10%;
}

#robot-try-again, #next-level {
	visibility: hidden;
	display: block;
}


/*Code output section*/
#code {
	visibility: hidden;
	background-color: #050b1f;
	color: #fbfaf5;
}

#code > #code-output {
	visibility: inherit;
	text-align: left;
	background-color: #050b1f;
	color: #6bcae2;
	padding-bottom: 10px;
	font-size: 17px;
}

#code-elements {
	visibility: hidden;
	position: absolute;
	bottom: 10%;
}

#code-elements > div {
	margin-top: 5px;
	margin-left: 10px;
	margin-right: 10px;
	text-align: left;
}

.highlighted {
	display: inline;
	color: #6bcae2;
}

#ifcode_body {
	top: 0px;
	bottom: 0px;
	right: 0px;
	left: 0px;
	margin: 0px;
	font-family: sans-serif;
	background-image: linear-gradient(to right, #c7d8e7, rgb(248,248,255));
}
#content {
	margin: 0px 60px 10px 65px;
}

.ifcode_pre {
	padding: 15px 50px 15px 50px;
	background-color: #050b1f;
	color: #6bcae2;
	box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.2);
	border-radius: 10px;
}
#toGameButton {
	display: inline-block;
	background-color: #050b1f;
	color: #6bcae2;
	text-align: center;
	font: normal 20px verdana;
	padding: 15px;
	margin-top: 15px;
	margin-bottom: 15px;
	box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.2);
	border: solid 1px rgba(0,0,0,0.2);
	border-radius: 10px;
}

#toGameButton:hover {
	background-color: #fbfaf5;
}

#fontsizer {  
	display: inline-block;	
	font-family: calibri;
}
#fontsizer ul {
	list-style-type: none;
	margin: 0px;
	padding: 0px;
}

#fontsizer li {
	text-align: center;
        display: inline-block;
	margin: 0;
	float: left;
	width: 50px;		
	height: 50px;
	line-height: 50px;
	background: #050b1f;
	color: #6bcae2;
}

#fontsizer li:hover {
	background: #fbfaf5;
}

#fontsizer li:nth-child(2) {
	font-size: 20px;
}

#fontsizer li:nth-child(3) {
	font-size: 30px;
	border-radius: 0px 10px 10px 0px;
}

#fontsizer li:nth-child(1) {
	border-radius: 10px 0px 0px 10px;
}

/* BASICS */

.CodeMirror {
  /* Set height, width, borders, and global font properties here */
  font-family: monospace;
  height: 300px;
  color: black;
  direction: ltr;
}

/* PADDING */

.CodeMirror-lines {
  padding: 4px 0; /* Vertical padding around content */
}
.CodeMirror pre.CodeMirror-line,
.CodeMirror pre.CodeMirror-line-like {
  padding: 0 4px; /* Horizontal padding of content */
}

.CodeMirror-scrollbar-filler, .CodeMirror-gutter-filler {
  background-color: transparent; /* The little square between H and V scrollbars */
}

/* GUTTER */

.CodeMirror-gutters {
  border-right: 1px solid #ddd;
  background-color: #f7f7f7;
  white-space: nowrap;
}
.CodeMirror-linenumbers {}
.CodeMirror-linenumber {
  padding: 0 3px 0 5px;
  min-width: 20px;
  text-align: right;
  color: #999;
  white-space: nowrap;
}

.CodeMirror-guttermarker { color: black; }
.CodeMirror-guttermarker-subtle { color: #999; }

/* CURSOR */

.CodeMirror-cursor {
  border-left: 1px solid black;
  border-right: none;
  width: 0;
}
/* Shown when moving in bi-directional text */
.CodeMirror div.CodeMirror-secondarycursor {
  border-left: 1px solid silver;
}
.cm-fat-cursor .CodeMirror-cursor {
  width: auto;
  border: 0 !important;
  background: #7e7;
}
.cm-fat-cursor div.CodeMirror-cursors {
  z-index: 1;
}
.cm-fat-cursor-mark {
  background-color: rgba(20, 255, 20, 0.5);
  animation: blink 1.06s steps(1) infinite;
}
.cm-animate-fat-cursor {
  width: auto;
  border: 0;
  animation: blink 1.06s steps(1) infinite;
  background-color: #7e7;
}
@keyframes blink {
  0% {}
  50% { background-color: transparent; }
  100% {}
}

/* Can style cursor different in overwrite (non-insert) mode */
.CodeMirror-overwrite .CodeMirror-cursor {}

.cm-tab { display: inline-block; text-decoration: inherit; }

.CodeMirror-rulers {
  position: absolute;
  left: 0; right: 0; top: -50px; bottom: 0;
  overflow: hidden;
}
.CodeMirror-ruler {
  border-left: 1px solid #ccc;
  top: 0; bottom: 0;
  position: absolute;
}

/* DEFAULT THEME */

.cm-s-default .cm-header {color: blue;}
.cm-s-default .cm-quote {color: #090;}
.cm-negative {color: #d44;}
.cm-positive {color: #292;}
.cm-header, .cm-strong {font-weight: bold;}
.cm-em {font-style: italic;}
.cm-link {text-decoration: underline;}
.cm-strikethrough {text-decoration: line-through;}

.cm-s-default .cm-keyword {color: #708;}
.cm-s-default .cm-atom {color: #219;}
.cm-s-default .cm-number {color: #164;}
.cm-s-default .cm-def {color: #00f;}
.cm-s-default .cm-variable,
.cm-s-default .cm-punctuation,
.cm-s-default .cm-property,
.cm-s-default .cm-operator {}
.cm-s-default .cm-variable-2 {color: #05a;}
.cm-s-default .cm-variable-3, .cm-s-default .cm-type {color: #085;}
.cm-s-default .cm-comment {color: #a50;}
.cm-s-default .cm-string {color: #a11;}
.cm-s-default .cm-string-2 {color: #f50;}
.cm-s-default .cm-meta {color: #555;}
.cm-s-default .cm-qualifier {color: #555;}
.cm-s-default .cm-builtin {color: #30a;}
.cm-s-default .cm-bracket {color: #997;}
.cm-s-default .cm-tag {color: #170;}
.cm-s-default .cm-attribute {color: #00c;}
.cm-s-default .cm-hr {color: #999;}
.cm-s-default .cm-link {color: #00c;}

.cm-s-default .cm-error {color: #f00;}
.cm-invalidchar {color: #f00;}

.CodeMirror-composing { border-bottom: 2px solid; }

/* Default styles for common addons */

div.CodeMirror span.CodeMirror-matchingbracket {color: #0b0;}
div.CodeMirror span.CodeMirror-nonmatchingbracket {color: #a22;}
.CodeMirror-matchingtag { background: rgba(255, 150, 0, .3); }
.CodeMirror-activeline-background {background: #e8f2ff;}

/* STOP */

/* The rest of this file contains styles related to the mechanics of
   the editor. You probably shouldn't touch them. */

.CodeMirror {
  position: relative;
  overflow: hidden;
  background: white;
}

.CodeMirror-scroll {
  overflow: scroll !important; /* Things will break if this is overridden */
  /* 50px is the magic margin used to hide the element's real scrollbars */
  /* See overflow: hidden in .CodeMirror */
  margin-bottom: -50px; margin-right: -50px;
  padding-bottom: 50px;
  height: 100%;
  outline: none; /* Prevent dragging from highlighting the element */
  position: relative;
}
.CodeMirror-sizer {
  position: relative;
  border-right: 50px solid transparent;
}

/* The fake, visible scrollbars. Used to force redraw during scrolling
   before actual scrolling happens, thus preventing shaking and
   flickering artifacts. */
.CodeMirror-vscrollbar, .CodeMirror-hscrollbar, .CodeMirror-scrollbar-filler, .CodeMirror-gutter-filler {
  position: absolute;
  z-index: 6;
  display: none;
  outline: none;
}
.CodeMirror-vscrollbar {
  right: 0; top: 0;
  overflow-x: hidden;
  overflow-y: scroll;
}
.CodeMirror-hscrollbar {
  bottom: 0; left: 0;
  overflow-y: hidden;
  overflow-x: scroll;
}
.CodeMirror-scrollbar-filler {
  right: 0; bottom: 0;
}
.CodeMirror-gutter-filler {
  left: 0; bottom: 0;
}

.CodeMirror-gutters {
  position: absolute; left: 0; top: 0;
  min-height: 100%;
  z-index: 3;
}
.CodeMirror-gutter {
  white-space: normal;
  height: 100%;
  display: inline-block;
  vertical-align: top;
  margin-bottom: -50px;
}
.CodeMirror-gutter-wrapper {
  position: absolute;
  z-index: 4;
  background: none !important;
  border: none !important;
}
.CodeMirror-gutter-background {
  position: absolute;
  top: 0; bottom: 0;
  z-index: 4;
}
.CodeMirror-gutter-elt {
  position: absolute;
  cursor: default;
  z-index: 4;
}
.CodeMirror-gutter-wrapper ::selection { background-color: transparent }
.CodeMirror-gutter-wrapper ::-moz-selection { background-color: transparent }

.CodeMirror-lines {
  cursor: text;
  min-height: 1px; /* prevents collapsing before first draw */
}
.CodeMirror pre.CodeMirror-line,
.CodeMirror pre.CodeMirror-line-like {
  /* Reset some styles that the rest of the page might have set */ border-radius: 0;
  border-width: 0;
  background: transparent;
  font-family: inherit;
  font-size: inherit;
  margin: 0;
  white-space: pre;
  word-wrap: normal;
  line-height: inherit;
  color: inherit;
  z-index: 2;
  position: relative;
  overflow: visible;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-variant-ligatures: contextual;
  font-feature-settings: "calt";
  font-variant-ligatures: contextual;
}
.CodeMirror-wrap pre.CodeMirror-line,
.CodeMirror-wrap pre.CodeMirror-line-like {
  word-wrap: break-word;
  white-space: pre-wrap;
  word-break: normal;
}

.CodeMirror-linebackground {
  position: absolute;
  left: 0; right: 0; top: 0; bottom: 0;
  z-index: 0;
}

.CodeMirror-linewidget {
  position: relative;
  z-index: 2;
  padding: 0.1px; /* Force widget margins to stay inside of the container */
}

.CodeMirror-widget {}

.CodeMirror-rtl pre { direction: rtl; }

.CodeMirror-code {
  outline: none;
}

/* Force content-box sizing for the elements where we expect it */
.CodeMirror-scroll,
.CodeMirror-sizer,
.CodeMirror-gutter,
.CodeMirror-gutters,
.CodeMirror-linenumber {
  box-sizing: content-box;
}

.CodeMirror-measure {
  position: absolute;
  width: 100%;
  height: 0;
  overflow: hidden;
  visibility: hidden;
}

.CodeMirror-cursor {
  position: absolute;
  pointer-events: none;
}
.CodeMirror-measure pre { position: static; }

div.CodeMirror-cursors {
  visibility: hidden;
  position: relative;
  z-index: 3;
}
div.CodeMirror-dragcursors {
  visibility: visible;
}

.CodeMirror-focused div.CodeMirror-cursors {
  visibility: visible;
}

.CodeMirror-selected { background: #d9d9d9; }
.CodeMirror-focused .CodeMirror-selected { background: #d7d4f0; }
.CodeMirror-crosshair { cursor: crosshair; }
.CodeMirror-line::selection, .CodeMirror-line > span::selection, .CodeMirror-line > span > span::selection { background: #d7d4f0; }
.CodeMirror-line::-moz-selection, .CodeMirror-line > span::-moz-selection, .CodeMirror-line > span > span::-moz-selection { background: #d7d4f0; }

.cm-searching {
  background-color: #ffa;
  background-color: rgba(255, 255, 0, .4);
}

/* Used to force a border model for a node */
.cm-force-border { padding-right: .1px; }

@media print {
  /* Hide the cursor when printing */
  .CodeMirror div.CodeMirror-cursors {
    visibility: hidden;
  }
}

/* See issue #2901 */
.cm-tab-wrap-hack:after { content: ''; }

/* Help users use markselection to safely style text background */
span.CodeMirror-selectedtext { background: none; }

